#socialdata{
    .connected{
        i{
            &.verified{
                position: relative;
                &::after{
                    content: "\2713";
                    position: absolute;
                    color: green;
                    font-size: 20px;
                    font-weight: bold;
                    top: 10px;
                }
            }
        }
    }
}

